<template>
  <div class="user-List">
    <info ref="info"></info>
    <bread></bread>
    <div class="search">
      <Row>
        <Col span="3">
        <div style="display: flex; margin-right: 5px;">
          <label style="width: 60px;margin-top: 5px;">名称：</label>
          <Input v-model.trim="name" placeholder="使用名称查询">
          </Input>
        </div>
        </Col>
        <Col span="3">
        <div style="display: flex; margin-right: 5px;">
          <label style="width: 80px;margin-top: 5px;">手机号：</label>
          <Input placeholder="使用手机号查询" v-model.trim="username"></Input>
        </div>
        </Col>
        <Col span="3">
        <DatePicker type="daterange" :options="options2" v-model="date" placeholder="选择日期"></DatePicker>
        </Col>
        <Col span="3">
        <div>
          <a href="javascript:void (0)"  @click="search" class="search_btn">查询 </a>
        </div>
        </Col>
      </Row>
    </div>
    <table>
      <tr>
        <th>名称 <span class="people_info">(点击查看详情)</span></th>
        <th>余额</th>
        <th>手机号</th>
        <th>账号数</th>
        <th>注册时间</th>
        <!--<th>状态</th>-->
        <th style="width:20em;">审核</th>
        <th style="width: 10em;">操作</th>
      </tr>
      <tr class="td" v-for="(item,key) in data">
        <td><a href="javascript:void(0)" class="nickname" @click="seeInfo(item)" style="color:#2277da">{{item.nickname}}</a></td>
        <td>{{item.wallet}}</td>
        <td>{{item.username}}</td>
        <td>{{item.app_num}}</td>
        <td>{{item.register_time}}</td>
        <!--<td>{{item.register_time}}</td>-->
        <td>
            <a href="javascript:void(0)" class="info" @click="apply(item,2)" v-if="item.is_agency === 1">通过</a>
            <a href="javascript:void(0)" class="info no_info" @click="apply(item,3)" v-if="item.is_agency === 1" style="margin-left: 15px">驳回</a>
            <span v-if="item.is_agency !== 1">{{item.is_agency | applyState}}</span>
        </td>
        <td>
          <i-switch size="large" v-model="item.is_forbidden" @on-change="chan(item,key)" true-value="0" false-value="1">
            <span slot="open">启用</span>
            <span slot="close">禁用</span>
          </i-switch>
          <a href="javascript:void(0)" @click="deleted(item,key)" style="display: inline-block;margin-left:20px">
            <img src="../../assets/garbage-icon.png" alt="">
          </a>
        </td>
      </tr>
    </table>
    <p v-if="data.length===0" class="td_data">
      {{msg}}
    </p>
    <!--<page></page>-->
    <div class="ter_page">
      <Page :total="num" :current="current" :page-size="currentPage" size="small" show-total show-sizer @on-change="pageNnumber" :page-size-opts="[10,20,30,40]" @on-page-size-change="pageRow">
      </Page>
    </div>
    <detailPage v-show="false"></detailPage>
  </div>
</template>
<script>
  import bread from '../bread.vue'
  import page from '../page.vue'
  import info from '../info.vue'
  import detailPage from './detailPage.vue'
  export default {
    data() {
      return {
        msg: "正在加载....",
        data: [],
        select3: "",
        formItem: "",
        name: "",
        username:"",
        //      当前页码
        currentPage: 10,
        //      每页条数
        current: 1,
        startTime:"",
        endTime:"",
        date:'',
        //      总条数
        num: 0,
        options2: {
          shortcuts: [
            {
              text: '最近一周',
              value () {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                return [start, end];
              }
            },
            {
              text: '最近一个月',
              value () {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                return [start, end];
              }
            },
            {
              text: '最近三个月',
              value () {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                return [start, end];
              }
            }
          ]
        }
      }
    },
    methods: {
//      审核代理商
      apply (item,val) {
        this.post("system/Agency/update_agent", {
          id: item.id,
          is_agency: val
        }).then(res => {
            if (res.data.code === 10000) {
              this.getUserList();
              this.$Modal.success({
                title: "审核成功",
                content: '成功通过审核'
              });
            } else {
              this.$Modal.success({
                title: "审核失败",
                content: res.data.msg
              });
            }
        })
      },
      //    封装

      getUserList(currentPage, current,startTime,endTime,name,username) {
        this.post("system/Agency/get_agent_list ", {
          page: this.current,
          limit_num:this.currentPage,
          name:name,
          username:username,
          start_time:startTime,
          end_time:endTime,
        }).then(res => {
          if(res.data.code === 10000) {
            this.data = res.data.data;
            this.num = res.data.number;
            this.data.forEach(item => {
              let time = new Date(item.register_time * 1000);
              item.is_forbidden = `${item.is_forbidden}`
              item.register_time = time.getFullYear()+'/'+ (time.getMonth()-0+1)+'/'+time.getDate() + ' ' + time.getHours()+':'+time.getMinutes();
            })
          } else {
            this.data = "暂无数据"
            const content = res.data.msg
            this.$Modal.error({
              title: "获取失败",
              content: content
            });
          }
        });
      },
//    搜索
      search(val){
        if(this.date[0]&&this.date[1]){
          this.startTime=Math.ceil(new Date(this.date[0]).getTime()/1000);
          this.endTime=Math.ceil(new Date(this.date[1]).getTime()/1000);
        }
        this.getUserList(this.name,this.username,this.startTime,this.endTime);
      },
      pageNnumber(val) {
        this.current = val
        this.getUserList(this.current,this.currentPage);
      },
      pageRow(val) {
        this.currentPage = val
        this.getUserList(this.current,this.currentPage);
      },
      seeInfo(index) {
        let url = "agentInfo/" + index.id
        this.$router.push({
          path: url
        })
      },
      chan(index) {
        let custom = "custom"
        this.$Modal.confirm({
          title: '是否修改',
          content: '<p>确定要启用或关闭该用户吗</p>',
          onOk: () => {
            this.post("system/Agency/save_agent_state ", {
              id: index.id,
              is_forbidden: index.is_forbidden
            }).then(res => {
              if(res.data.code === 10000) {
                const content = res.data.msg
                this.$Modal.success({
                  title: "修改成功",
                  content: content
                });
              } else {
                const content = res.data.msg
                this.$Modal.error({
                  title: "修改失败",
                  content: content
                });
              }
            });
          },
        });
      },
      change(index) {
        let url = '/userInfo/' + index
        this.$router.push(url);
      },
      deleted(index) {
        this.$Modal.confirm({
          title: '退出',
          content: '<p>确定要删除该用户吗</p>',
          onOk: () => {
            this.post("system/Agency/delete_agent", {
              id: index.id
            }).then(res => {
              if (res.data.code === 10000) {
                const content = res.data.msg
                this.$Modal.error({
                  title: "删除成功",
                  content: content
                });
              } else {
                const content = res.data.msg_test
                this.$Modal.error({
                  title: "删除失败",
                  content: content
                });
              }
            });

          },
          onCancel: () => {}
        });
      },
    },
    filters: {
      applyState (val) {
        switch (val) {
          case 2 :
            return "已通过"
          case 3 :
            return "未通过"
        }
      }
    },
    created() {
      this.getUserList()
    },
    components: {
      bread,
      page,
      info,
      detailPage
    }
  }
</script>
<style scoped="scoped">
  .search_btn {
    display: inline-block;
    padding: 5px 20px;
    background-color: #6098ff;
    border-radius: 5px;
    color: white;
  }

  .search_btn:hover {
    color: white;
    text-decoration: none;
  }
  .search{
    margin-right: 20px;
    margin-bottom: 30px;
  }

  .user-List {
    width: 96%;
    margin: 0 auto;
  }

  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .list {
    padding: 20px 0
  }

  table {
    width: 100%;
    font-size: 14px
  }

  th {
    padding: 10px 20px;
    background: #F5F5F5;
    border: 1px solid #E9ECEF;
    text-align: center;
  }

  .td>td {
    padding: 6px 10px;
    border-bottom: 1px solid #f2f2f2;
    text-align: center;
  }

  .info {
    color: white;
    background: #6098ff;
    width: 85px;
    height: 30px;
    line-height: 30px;
    display: inline-block;
    text-align: center;
    border-radius: 4px;
  }
  .no_info{
      background-color: white;
      border: 1px solid #6098ff;
      color:#6098ff ;
  }
  td {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    border: 1px solid #E9ECEF;
    background: white;
  }

  .td_data {
    width: 100%;
    padding: 20px 10px;
    text-align: center;
    border-bottom: 1px solid #f2f2f2;
  }

  a:hover {
    opacity: 0.6;
    text-decoration: none
  }

  .ter_page {
    text-align: center;
    margin-top: 40px;
  }
  .people_info{
    font-size: 1em;
    font-weight: normal;
    color: #444;
    letter-spacing: 0.2em;
    margin-left: 5px;
  }
  .nickname{
    color: black;
  }
  .nickname:hover{
    color: #6098FF;
  }
</style>



